import React,{useState} from "react";
import AddIcon from "@material-ui/icons/Add";
import Fab from "@material-ui/core/Fab";
import Zoom from "@material-ui/core/Zoom";

function CreateArea(props) {
    const [isExpanded, setExpanded] = useState(false);
    const [note, setNote] = useState({
        title:"",
        content:""
    });
    function handleChange(event){
        const {name, value} = event.target;
        setNote(prevValue => {
            return {
                ...prevValue,
                [name]:value
            };
        });
    }
    function handleClick(event){
        if(note.title !== "" || note.content !== "") props.onAdd(note);
        setNote({
            title:"",
            content:""
        })
        event.preventDefault();
    }
    function expand(){
        setExpanded(true);
    }

  return (
    <div>
      <form className="create-note">
        {isExpanded && (<input name="title" onChange={handleChange}  placeholder="Title" value={note.title} />)}
        
        <textarea onClick={expand} name="content" onChange={handleChange} placeholder="Take a note..." value={note.content} rows={isExpanded ? 3 : 1} />
        <Zoom in={isExpanded}>
          <Fab onClick={handleClick}>
            <AddIcon />
          </Fab>
        </Zoom>
      </form>
    </div>
  );
}

export default CreateArea;
